.grid {
  margin: 0 -1rem;

  @include respond-to('medium') {
    display: flex;
  }

  &--small-grid {
    display: flex;
  }

  &.wrap {
    flex-wrap: wrap;
  }

  &__cell {
    flex: 1;
    margin: .5rem;
    padding: .5rem;
  }

  &--flexCells > .grid__cell {
    display: flex;
  }

  &--top {
    align-items: flex-start;
  }

  &--bottom {
    align-items: flex-end;
  }

  &--center {
    align-items: center;
  }

  &--justify-center {
    justify-content: center;
  }

  &-cell--top {
    align-self: flex-start;
  }

  &-cell--bottom {
    align-self: flex-end;
  }

  &-cell--center {
    align-self: center;
  }

  &-cell--autoSize {
    flex: none;
  }
}

.bordered {
  @include respond-to('medium') {
    border-left: 1px solid $light-gray;

    &:first-child {
      border: 0;
    }
  }
}
